import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormControl, Validators } from '@angular/forms';

import { LoginPage } from '../login/login';
import { OtpPage } from '../otp/otp';
import { RestProvider } from '../../providers/rest/rest';

/**
 * Generated class for the ForgotPassPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-forgot-pass',
  templateUrl: 'forgot-pass.html',
})
export class ForgotPassPage {

  user: any = {};

  constructor(public navCtrl: NavController, public navParams: NavParams, public restProvider:RestProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ForgotPassPage');
  }

  email = new FormControl('', [Validators.required, Validators.email]);

  getErrorMessage(){
    return this.email.hasError('required') ? 'You must enter a value' :
    this.email.hasError('email')? 'make sure your email  @example.com': '';
  }

  hide = true;

  ForgotPassword(){
    if(this.user.email !== null && this.user.email !== ""){
      localStorage.forPassEmail = this.user.email;

      this.restProvider.__registration(this.user).then((result) => {
        console.log(result);
        console.log('going to the OTP page');
        this.navCtrl.push(OtpPage);
      }, (err) => {
        console.log(err);

      });

    }else{
      this.getErrorMessage();
      alert('Please enter your email');
    }

  }



  goToLogin(){
    console.log('going back to login');
    this.navCtrl.push(LoginPage);
  }

}



